/*BORDER RADIUS*/
.desktop {
  display: flex;
  flex-direction: column;
  gap: 0px;
  border-radius: 4px;
  overflow: hidden;
}
.desktop img {
  max-width: 680px;
  width: 100%;
}

.form h2 {
  text-align: center;
}
.form video {
  max-width: 680px;
  width: 100%;
  border-radius: 8px;
}

.mobile {
  background-color: #222222;
  text-align: center;
}
.mobile video {
  border-radius: 8px;
  overflow: hidden;
  max-width: 250px;
}

.contact {
  padding: 80px 0px;
}
.contact .contact__links {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.contact .contact__links .cs-link {
  display: block;
  color: #19A0FC;
}
.contact .contact__links .cs-link::after {
  content: " " url(/img/arrow-up-right.svg);
}