.desktop {
  border-radius: 4px;
  flex-direction: column;
  gap: 0;
  display: flex;
  overflow: hidden;
}

.desktop img {
  max-width: 680px;
  width: 100%;
}

.form h2 {
  text-align: center;
}

.form video {
  max-width: 680px;
  width: 100%;
  border-radius: 8px;
}

.mobile {
  text-align: center;
  background-color: #222;
}

.mobile video {
  max-width: 250px;
  border-radius: 8px;
  overflow: hidden;
}

.contact {
  padding: 80px 0;
}

.contact .contact__links {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.contact .contact__links .cs-link {
  color: #19a0fc;
  display: block;
}

.contact .contact__links .cs-link:after {
  content: " " url("arrow-up-right.75d75e4c.svg");
}

